<template>
    <div class="coupon my-coupon my-coupon-refuse">
        <a target="_blank" class="coupon-href" :href="'/id'+item.action.id"></a>
        <div class="c-link"><a target="_blank" :href="'/id'+item.action.id">
            <svg class="icon">
                <use xlink:href="@/assets/img/mob-sprite.svg#link"></use>
            </svg></a></div>
          <div class="coupon-inner">
            <div class="coupon-img" :style="{ backgroundImage: 'url(//api.zemskidki.ru/'+item.action.image+')' }"></div>
            <div class="discount-flag" @click="openAverageCheck()">
              <span class="percent">−{{ item.action.discount }}%</span>
              <span class="text-size-s text-normal sum">≈{{item.action.realSavings}} Р</span>
            </div>
            <div class="c-favourite" ref="favoriteBtn" @click="setFavorite(item)">
                <svg :class="{'icon-fav': true, 'fav': true, 'active': item.action.favorite}">
                    <use xlink:href="@/assets/img/mob-sprite.svg#fav"></use>
                </svg>

            </div>
            <div class="coupon-stat">
                <div class="views stat-item">
                    <svg class="icon-views">
                        <use xlink:href="@/assets/img/mob-sprite.svg#views"></use>
                    </svg><span class="count">{{ item.action.showCounterFormat }}</span>
                </div>
                <div class="purchases stat-item" v-if="item.action.statistics.sold > 0">
                    <svg class="icon-cart">
                        <use xlink:href="@/assets/img/mob-sprite.svg#cart"></use>
                    </svg><span class="count">{{ item.action.statistics.sold }}</span>
                </div>
                <div class="sum stat-item" style="display: none">
                    <svg class="icon-coins">
                        <use xlink:href="@/assets/img/mob-sprite.svg#coins"></use>
                    </svg><span class="count">22 128 Р</span>
                </div>
                <!--<div class="comments stat-item" v-if="item.action.countOpinions > 0">
                    <svg class="icon-comment">
                        <use xlink:href="@/assets/img/mob-sprite.svg#comment"></use>
                    </svg><span class="count">{{  item.action.countOpinions }}</span>
                </div>-->
            </div>
            <div class="coupon-name">{{ item.action.name }}</div>
            <div class="top">
                <div class="company">{{ item.action.provider.name }}</div>
            </div>
        </div>
        <div class="separator-block">
          <div class="separator">
              <svg class="icon-separator">
                  <use xlink:href="@/assets/img/mob-sprite.svg#separator"></use>
              </svg>
              <svg class="icon-scissors">
                  <use xlink:href="@/assets/img/mob-sprite.svg#scissors"></use>
              </svg>
          </div>
        </div>
        <div class="status" v-if="item.status.code === 'valid'">Активен</div>
        <div class="status" v-if="item.status.code === 'extinguished'">Использован {{ item.status.date }}</div>
        <div class="status" v-if="item.status.code === 'expired'">Просрочен {{ item.status.date }}</div>
        <div class="status" v-if="item.status.code === 'canceled'">Аннулирован {{ item.status.date }}</div>
        <div class="status" v-if="item.status.code === 'withdrawn'">Отменён {{ item.status.date }}</div>

        <ul class="actions">
            <li class="city"><a>
                <svg class="icon">
                    <use xlink:href="@/assets/img/mob-sprite.svg#map-track"></use>
                </svg><span>{{ item.city }}</span></a></li>
            <li class="view-story"  @click="openCouponHistory(item)"><a href="javascript:void(0)">
                <svg class="icon">
                    <use xlink:href="@/assets/img/mob-sprite.svg#view-history"></use>
                </svg><span>Посмотреть историю</span></a></li>
            <li class="refuse" v-if="item.status.code === 'valid'" @click="openRepaymentAnnul(item)"><a href="javascript:void(0)">
                <svg class="icon">
                    <use xlink:href="@/assets/img/mob-sprite.svg#c-remove"></use>
                </svg><span>Аннулировать</span></a></li>

        </ul>
        <div class="button-block" v-if="item.status.code === 'valid'">
            <button class="btn btn-green" @click="openRepaymentRepay(item)"><span>Погасить купон</span></button>
        </div>

    </div>
</template>

<script>
    import Popup from '@/components/modal/Popup.vue';
    import PopupInfoBlock from '@/components/modal/PopupInfoBlock.vue';
    import {flyToFavorite} from '@/helpers/animate';
    import CouponCancel from '@/components/modal/CouponCancel.vue';
    import CouponClaim from '@/components/modal/CouponClaim.vue';
    import RepeatSendInfo from '@/components/modal/RepeatSendInfo.vue';
    import CheckoutFail from '@/components/modal/CheckoutFail.vue';
    import {mapState} from 'vuex';
    import Tooltip from '@/components/Tooltip.vue';

    export default {
        name: "RepaymentListItem",
        components: {
            Popup,
            PopupInfoBlock,
            CouponCancel,
            CouponClaim,
            RepeatSendInfo,
            CheckoutFail,
            Tooltip,
        },
        props: {
            item: {
                default: function () {
                    return {}
                }
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        },
        methods: {
          openAverageCheck(){
            this.$store.commit('popups/open', {name: this.popups.averageCheck.name});
          },
            openRepaymentAnnul: function (item) {
                this.$store.commit('popups/open', {name: this.popups.repaymentAnnul.name});
                this.$store.commit('partnerCoupons/setCurrent', item)

            },
            openRepaymentRepay: function (item) {
                this.$store.commit('popups/open', {name: this.popups.repaymentRepay.name});
                this.$store.commit('partnerCoupons/setCurrent', item)
            },
            openCouponHistory: function (item) {
                this.$store.commit('popups/open', {name: this.popups.couponHistory.name});
                this.$store.commit('partnerCoupons/setCurrent', item)
            }
        }
    }

</script>
