<template>
    <div>
        <div class="popup-header ph-long">
            <div class="popup-name">Аннулировать купон</div>
            <div class="popup-subtitle">{{ coupon.action.name }}</div>
            <div class="popup-h-info"><span>{{ coupon.action.provider.name}}</span></div>
            <div class="close" @click="close()"><span class="i icon-close">
                          <svg class="icon">
                            <use xlink:href="@/assets/img/mob-sprite.svg#close"></use>
                          </svg></span></div>
        </div>
        <div class="popup-content">
            <div class="inner">
                <div class="content-ttl">Причина аннулирования</div>
                <textarea-field  placeholder="Например, «Нет в наличии товара, ради которого покупатель оформил купон...»"
                                 v-model="text.value"
                                 :auto-focus="true"
                                 :is-error="text.error"
                                 :add-style="{height: 104 + 'px'}"
                                 @focus="removeError('text')"
                ></textarea-field>

                <div class="button-block">
                    <submit-btn tag="button"
                                class="btn"
                                @click.native="send()"
                                :type="buttonSend">
                        Аннулировать купон
                    </submit-btn>
                </div>
                <div class="underbtn"><span>Внимание! Аннулирование купона нельзя отменить!</span></div>
            </div>

        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import TextareaField from '@/components/forms/TextareaField.vue'
    import SubmitBtn from '@/components/forms/SubmitBtn.vue'

    export default {
        name: "RepaymentAnnul",
        components: {
            TextareaField,
            SubmitBtn
        },
        props: {
            coupon: {}
        },
        data: function() {
            return {
                text: {
                    value: '',
                    error: false,
                },
                buttonSend: 'normal'
            }
        },
        created() {

        },
        methods: {
            send() {
                if(this.text.value.length < 10) {
                    this.text.error = true;
                } else {
                    this.buttonSend = 'load';
                    this.$store.dispatch('partnerCoupons/sendRepaymentAnnul', {
                        text: this.text.value,
                        id:this.coupon.id
                    }).then(()=>{
                        this.buttonSend = 'ok';
                        setTimeout(()=>{
                            this.$store.commit('popups/close', {name: this.popups.repaymentAnnul.name});
                            this.buttonSend = 'normal';
                        }, 300);
                    });
                }

            },
            close() {
                this.$store.commit('popups/close', {name: this.popups.repaymentAnnul.name});
            },
            removeError(field){
                this[field].error = false;
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                masks: state => state.masks.items,
            }),
        },
        beforeDestroy(){
            this.$store.commit('popups/close', {name: this.popups.repaymentAnnul.name});
        }
    }
</script>

