<template>
    <div>
        <div class="popup-header ph-long">
            <div class="popup-name">Погасить купон</div>
            <div class="popup-subtitle">{{ coupon.action.name }}</div>
            <div class="popup-h-info"><span>{{ coupon.action.provider.name}}</span></div>
            <div class="close" @click="close()"><span class="i icon-close">
                          <svg class="icon">
                            <use xlink:href="@/assets/img/mob-sprite.svg#close"></use>
                          </svg></span></div>
        </div>
        <div class="popup-content">
            <div class="inner">
                <div class="content-ttl">Покупатель
                    <span class="content-sub-ttl">{{ coupon.user.fio }}</span></div>

                    <input-price
                            @input="showLoader()"
                            v-model="price.value"
                            @focus="removeError('price')"
                            :is-error="price.error"
                            :placeholder="'Сумма без скидки'"
                            :placeholder-input="'₽'"></input-price>

                    <input-styled
                                   name="percent"
                                   :value="percent.value"
                                   :mask="masks.percent"
                                   @input="showLoader()"
                                   :disabled="true"
                                   class="bottom-text"
                                   :isValidate="true"
                                   :is-error="percent.error"
                                   @focus="removeError('percent')"
                                   :placeholder="'Скидка для покупателя'"></input-styled>
                    <div class="inp-s-bottom-text">Эта скидка указана в вашей акции и изменить её нельзя...</div>

                    <textarea-field  placeholder="Напишите комментарий к погашению (необязательно)"
                                     v-model="text.value"
                                     :add-style="{height: 64 + 'px'}"
                                     @focus="removeError('text')"
                    ></textarea-field>

                <div class="discount-result"><span v-show="this.priceCalc>0">Сумма скидки: <strong>{{ number_format(discountCalc) }}<span class="rouble">{{ $store.getters['options/setRouble'] }}</span></strong> Итого в кассу: <strong>{{ number_format(priceCalc) }}<span class="rouble">{{ $store.getters['options/setRouble'] }}</span></strong></span></div>

                <div class="button-block">
                    <submit-btn tag="button"
                                class="btn btn--green"
                                @click.native="send()"
                                :type="buttonSend">
                        Погасить купон
                    </submit-btn>
                </div>

                <div class="underbtn"><span>Внимание! Погашение купона нельзя отменить!</span></div>

            </div>

        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import InputStyled from '@/components/forms/InputStyled.vue'
    import {isPhone} from '@/helpers/validate.js';
    import TextareaField from '@/components/forms/TextareaField.vue'
    import SubmitBtn from '@/components/forms/SubmitBtn.vue'
    import InputPrice from '@/components/forms/InputPrice.vue';
    import {number_format} from '@/helpers/formatted.js';
    export default {
        name: "RepaymentRepay",
        components: {
            InputStyled,
            TextareaField,
            SubmitBtn,
            InputPrice
        },
        props: {
            coupon: {},
        },
        data: function() {
            return {
                text: {
                    value: '',
                    error: false,
                },
                price: {
                    value: '',
                    error: false,
                },
                percent: {
                    value: this.coupon.action.discount,
                    error: false,
                },
                discount: {
                    value: '',
                    error: false,
                },
                loader: false,
                buttonSend: 'normal',
                discountType: 'percent',
                timeout: false,
            }
        },
        created() {

        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                masks: state => state.masks.items,
            }),
            discountTypeChangeText(){
                return this.discountType==='percent'?'рублях':'процентах'
            },
            discountCalc() {
                return this.discountType === 'percent'? (this.price.value.split(' ').join('') *  this.percent.value / 100).toFixed(0) : 0;
            },
            percentCalc() {
                return this.discountType !== 'percent' && this.price.value ? (this.discount.value.split(' ').join('') / this.price.value.split(' ').join('') *  100).toFixed(0) : 0;
            },
            priceCalc() {
                let price = this.price.value.split(' ').join('') - (this.discountType === 'percent'? (this.price.value.split(' ').join('') *  this.percent.value / 100).toFixed(0) : this.discount.value);
                return price > 0 ? price : 0;
            },
        },
        beforeDestroy(){
            this.$store.commit('popups/close', {name: this.popups.repaymentRepay.name});
        },
        methods: {
            send: function() {
                let error = false;
                if(this.discountType === 'percent' && this.percent.value.length === 0){
                    this.percent.error = true;
                    error = true;
                }
                if(this.discountType !== 'percent' && this.discount.value.length === 0){
                    this.discount.error = true;
                    error = true;
                }
                if(this.price.value.length === 0){
                    this.price.error = true;
                    error = true;
                }
                if(!error){
                    this.buttonSend = 'load';
                    this.$store.dispatch('partnerCoupons/sendRepaymentRepay', {
                        text: this.text.value,
                        price: this.price.value.split(' ').join(''),
                        discount: this.discount.value.split(' ').join(''),
                        percent: this.percent.value,
                        id:this.coupon.id
                    }).then(()=>{
                        this.buttonSend = 'ok';
                        setTimeout(()=>{
                            this.$store.commit('popups/close', {name: this.popups.repaymentRepay.name});
                            this.buttonSend = 'normal';
                        }, 300);
                    });
                }

            },
            close() {
                this.$store.commit('popups/close', {name: this.popups.repaymentRepay.name});
            },
            discountTypeChange(){
                if(this.discountType==='percent'){
                    this.discountType = 'sum'
                    this.percent.value = '';
                } else {
                    this.discountType = 'percent'
                    this.discount.value = '';
                }
            },
            removeError(field) {
                this[field].error = false;
            },
            number_format: function (v) {
                return number_format(v, 0, '', ' ')
            },
            showLoader(){
                this.loader = true;
                let self = this;
                if(this.timeout){
                    clearTimeout(this.timeout);
                }
                this.timeout = setTimeout(function(){
                    self.loader = false;
                },1000)
            }
        },

    }
</script>

<style scoped>
    .repay-calc {
        position: absolute;
        top: -34px;
        right: 0;
        width: 100%;
        height: 128px;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px dotted rgba(0, 0, 0, 0.1);
        border-radius: 4px;

    }

        .repay-calc__wrap {
            padding: 16px;
        }

            .repay-calc .r {
                font-size: 12px;
                line-height: 15px;
                color: rgba(84, 84, 84, 0.5);
            }

                .repay-calc .r + .r {
                    margin-top: 4px;
                }
</style>
