<template>
    <div class="content">
        <search-repayment></search-repayment>
        <div class="coupon-list-block only-list-name my-coupons-list-block">
            <div class="coupon-list-inner inner" v-if="repaymentCoupons.length > 0">
                <div class="list-name"><span class="txt">{{repaymentUser.name}}</span></div>
                <div class="coupon-list">
                    <repayment-list-item v-for="coupon in repaymentCoupons"
                                         :item="coupon"
                                         :key="coupon.id">
                    </repayment-list-item>
                </div>

                <popup :is-set-blank="true" :show="popups.repaymentAnnul.show" :name="popups.repaymentAnnul.name">
                    <repayment-annul class="cancel-coupon" :coupon="currentItem"></repayment-annul>
                </popup>


                <popup :is-set-blank="true" :show="popups.repaymentRepay.show" :name="popups.repaymentRepay.name">
                    <repayment-repay v-if="currentItem.action" class="redeem-coupon" :coupon="currentItem"></repayment-repay>
                </popup>


                <popup :is-set-blank="true" :show="popups.couponHistory.show" :name="popups.couponHistory.name">
                    <coupon-history class="coupon-history" :coupon="currentItem"></coupon-history>
                </popup>
            </div>

            <div class="coupon-list-inner inner no-coupons" v-else>
                <loader v-if="isLoad"></loader>
                <div v-else-if="isPhone(phoneSearch.value) && repaymentSend">
                    <div class="list-name"><span class="txt" v-if="repaymentUser">{{ repaymentUser.name }}</span><span v-else class="txt">Покупатель не найден</span></div>
                    <div class="list-txt">
                        <span v-if="!repaymentUser">Убедитесь, что вы указали правильный номер</span>
                        <span v-else-if="repaymentCouponsOther > 0">
                            На указанный номер оформлено купонов: <br>
                            В вашей компании — 0 шт.<br>
                            У других поставщиков – {{ repaymentCouponsOther }} шт.</span>

                        <span v-else>На указанный номер не оформлено ни одного купона</span>
                    </div>
                </div>
            </div>

        </div>
        <footer class="block-footer fr-footer">
          <div class="footer-inner">
            <div class="f-feedback f-txt"><span>Весь функционал кабинета поставщика<br>доступен в </span><a class="hf" href="#">полной версии сайта.</a></div>
            <div class="f-copyright f-txt">© 1980-2092 Алексей Земсков</div>
            <div class="f-logo"><a href="/">
                <svg class="icon-f-logo">
                  <use xlink:href="@/assets/img/mob-sprite.svg#f-logo"></use>
                </svg></a></div>
          </div>
      </footer>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import {isPhone} from '@/helpers/validate.js';
    import Loader from '@/components/Loader.vue';
    import RepaymentListItem from '@/components/partner/RepaymentListItem.vue';
    import CouponHistory from '@/components/modal/CouponHistory.vue';
    import RepaymentAnnul from '@/components/modal/RepaymentAnnul.vue';
    import RepaymentRepay from '@/components/modal/RepaymentRepay.vue';
    import Popup from '@/components/modal/Popup.vue';
    import SearchRepayment from '@/components/partner/SearchRepayment.vue'

    export default {
        metaInfo() {
            return {
                title: 'Поиск и погашение купонов',
                titleTemplate: this.$store.state.options.titleTemplate
            }
        },
        name: "Repayment",
        components: {
            Loader,
            RepaymentListItem,
            CouponHistory,
            RepaymentAnnul,
            RepaymentRepay,
            Popup,
            SearchRepayment,
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                isLoad: state =>  state.partnerCoupons.isLoad,
                currentItem: state =>  state.partnerCoupons.currentItem,
                phoneSearch: state =>  state.partnerCoupons.phoneSearch,
                repaymentCoupons: state =>  state.partnerCoupons.repaymentCoupons.items,
                repaymentCouponsOther: state =>  state.partnerCoupons.repaymentCoupons.other,
                repaymentUser: state =>  state.partnerCoupons.repaymentCoupons.user,
                repaymentSend: state =>  state.partnerCoupons.isSend,
            }),
        },
        methods: {
            isPhone(phone){
                return isPhone(phone)
            }
        }
    }
</script>

